.header__link {
  font-size: 18px;
  line-height: 22px;
  color: #fff;
  text-decoration: none;
  transition: opacity 0.5s ease-in-out;
}

.header__link:hover {
  opacity: 0.6;
}

@media screen and (max-width: 700px) {
  .header__link {
    font-size: 14px;
    line-height: 17px;
  }
}